.checkbox {
  display: none;
}

.checkbox + label {
  display: block;
  background: #ccc;
  border: 1px solid #555;
  text-align: center;
  border-radius: 10px;
  background-color: #c4c4c4;
  height: 64px;
  width: 191px;
  border: none;
  cursor: pointer;
  outline: none;
}

.checkbox + label:before {
  content: "";
  font-size: 20px;
}

.checkbox:checked + label {
  background-image: url(../img/svg/check.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.checkbox:checked + label:before {
  color: green;
}

.checkboxFollowers {
  display: none;
}

.checkboxFollowers + label {
  display: block;
  width: 100%;
  background: #f2f2f2;
  text-align: center;
  border-radius: 10px;
  line-height: 56px;
  height: 56px;
  border: none;
  cursor: pointer;
  outline: none;
}

.checkboxFollowers + label:before {
  content: "";
  font-size: 20px;
}

.checkboxFollowers:checked + label {
  border: 1px solid green;
}

.checkboxFollowers:checked + label:before {
  color: green;
}

